@import "./../css/variables.scss";

.container {
    min-height: 20rem;
    max-height: 80rem;
    height: calc(70rem - #{$headerHeight});
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    vertical-align: top;

    h1 {
        padding: 1.5rem 4.5rem;
        font-size: 2rem;
        border-radius: $borderRadius calc(#{$borderRadius});
    }

    p {
        font-size: 1rem;
        margin-left: 2rem;
        margin-right: 2rem;
    }
}

