@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;500;600;800&display=swap');

$Poppins: 'Poppins', sans-serif;

// colors
$dark: rgb(51, 46, 53);
$light: #fff;
$primary: rgb(70, 50, 87);
$bg: rgb(244,244,255);
$gray: rgb(158, 158, 158);

// spacing
$spacing-md: 16px;
$spacing-lg: 32px;

// border radius
$borderRadius: 12px;

// header height
$headerHeight: 150px;

@mixin breakpoint($point) {
    @if $point == md {
        // 768px
        @media (min-width: 48em) {
            @content;
        }
    }
}