img {
    max-width: 1080px;
    max-height: 512px;
    margin-top: 100px;
}

p {
    // white-space: pre-line;
    word-break: normal ;
    max-width: 1080px;
    flex-shrink: 1;
}

.hide {
    // animation: fadein 1s linear forwards;
    opacity: 0;
    display: none;
    // animation-delay: 2s;
}

.headerShow {
    animation: headerfadein ease 1s;
}

.textShow {
    animation: textfadein ease 2s;
}

.secondHeaderShow {
    animation: secondHeaderFadeIn ease 5s;
}

.secondTextShow {
    animation: secondTextFadeIn ease 6s;
}


.show {
    // -webkit-animation: display 2s linear forwards;
    animation: fadein ease 4s;
    // animation-delay: 2s;
    opacity: 1;
    transition: 0s;
}

@keyframes headerfadein {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes textfadein {
    0% {
        opacity: 0;
        transform: translateY(-10px);
    }
    30% {
            opacity: 0;
            transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadein {
    0% {
        opacity: 0;
        transform: translateY(-10px);
    }
    35% {
        opacity: 0;
        transform: translateY(-10px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes secondHeaderFadeIn {
    0% {
        opacity: 0;
        transform: translateY(-10px);
    }
    45% {
        opacity: 0;
        transform: translateY(-10px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes secondTextFadeIn {
    0% {
        opacity: 0;
        transform: translateY(-10px);
    }
    45% {
        opacity: 0;
        transform: translateY(-10px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
